
<template>
	<view>
	  <view>根据匹配规则展示，搜索到的蓝牙设备</view>
	  <view bindtap="chooseDevice" 
	    v-for="(item,index) in devices" 
	   :key="index" 
	    style="padding: 30rpx;border-bottom:2rpx solid #ebebeb; {{(connectedDevice &&connectedDevice.deviceId == item.deviceId)?'background:#ebebeb':''}}">
	    {{item.name}}
	  </view>
	</view>
	
	
	<view :hidden="!connectedDevice" class="wifi-btn" bindtap="enableFormVisible">
	  wifi
	</view>
	
	<view class="mask" :hidden="!formVisible">
	  <view class="close" bindtap="disableFormVisible">关闭</view>
	  <view>
	    wifi名称：
	    <input bindinput="inputEvent" data-key='wifiName' :value="wifiName"/>
	  </view>
	  <view>
	    wifi密码：
	    <input bindinput="inputEvent" data-key='wifiPass' value="{{wifiPass}}"/>
	  </view>
	
	  <button bindtap="submitWifiInfo">提交</button>
	</view>
</template>
 
<script>
	const plugin = requirePlugin('btutil-plugin')
	Page({
	  data: {
	    devices: [], // 匹配我们提供的蓝牙
	    connectedDevice: null, // 当前连接蓝牙设备信息
	    wifiName: 'tjl2021',
	    wifiPass: '88888888',
	    formVisible: false,
	  },
	
	  onLoad() {
	    // 正式环境建议关闭log，开发环境打开log方便调试
	    plugin.setLogDebug(true) // 是否打印log
	    plugin.setBluetoothNamePrefix(['YK', 'wu', '150'])
	    this.searchDevices()
	  },
	
	  /**
	   * 搜索蓝牙设备，并将搜索到的蓝牙设备设置到data，用于列表展示
	   */
	  searchDevices() {
	    plugin.searchDevices(devices => {
	      this.setData({
	        devices
	      })
	    })
	  },
	
	  /**
	   * 根据设备Id（deviceId）与蓝牙设备建立连接
	   * @param {String} deviceId 
	   */
	  createBLEConnection(deviceId) {
	    wx.showLoading({
	      title: '建立连接中',
	    })
	    plugin.createBLEConnection({deviceId}).then(res => {
	      console.log('完成建立连接等一系列操作')
	
	      this.enableFormVisible()
	    }).catch(err => {
	      console.log('建立连接失败', err)
	    }).finally(() => {
	      wx.hideLoading()
	    })
	  },
	
	  /**
	   * 提交Wifi信息（wifiName，wifiPass）给插件，等待基站处理。
	   * 
	   * 配网说明：
	   * 由于目前基站配网成功与否，都没有给小程序插件指令反馈，
	   * 故小程序插件无法通知配网是否成功，需要使用者自行根据基站灯光颜色判断配网成功与否
	   * 
	   * 基站变绿灯，则配网成功；
	   * 基站变红灯，则配网失败；
	   */
	  submitWifiInfo() {
	    plugin.submitWifiInfo({
	      wifiName: this.data.wifiName,
	      wifiPass: this.data.wifiPass,
	    }).then(() => {
	      wx.showToast({
	        title: '数据成功发送给基站，请等待基站处理',
	        icon: 'none',
	        duration: 6000
	      })
	    }).catch(err => {
	      console.log(err)
	    })
	  },
	
	  disableFormVisible() {
	    this.setFormVisible(false)
	  },
	
	  enableFormVisible() {
	    this.setFormVisible(true)
	  },
	
	  setFormVisible(formVisible) {
	    this.setData({
	      formVisible
	    })
	  },
	
	  // 输入
	  inputEvent(e) {
	    this.setData({[e.currentTarget.dataset.key]: e.detail.value})
	  },
	
	
	  /**
	   * 选择蓝牙设备
	   * @param {Event} e
	   */
	  chooseDevice(e){
	    const i = e.currentTarget.dataset.index
	    const currentDevice = this.data.devices[i]
	    const {deviceId} = currentDevice
	
	    this.setData({
	      connectedDevice: currentDevice
	    })
	
	    this.createBLEConnection(deviceId)
	  },
	})
</script>
 
<style>
	/* pages/index/index.wxss */
	.close {
	  position: absolute;
	  top: 0;
	  right: 0;
	  width: 90rpx;
	  height: 90rpx;
	  background: #ebebeb;
	  text-align: center;
	  line-height: 90rpx;
	}
	
	.mask {
	  padding: 90rpx 20rpx 0;
	  position: fixed;
	  height: 40vh;
	  width: 80vw;
	  top: 30vh;
	  left: 10vw;
	  background: #fff;
	  box-shadow: 0 0 20rpx rgba(0, 0, 0, .4);
	  box-sizing: border-box;
	}
	
	input{
	  margin-bottom: 30rpx;
	  height: 60rpx;
	  padding: 0 30rpx;
	  border-bottom: 2rpx solid #ebebeb;
	}
	
	.wifi-btn{
	  position: fixed;
	  top: 40rpx;
	  right: 40rpx;
	  background: #fff;
	  box-shadow: 0 0 20rpx rgba(0, 0, 0, .4);
	  box-sizing: border-box;
	  width: 90rpx;
	  height: 90rpx;
	  background: #bbbbbb;
	  text-align: center;
	  line-height: 90rpx;
	}
</style>